const getFieldOptionByMethod = (method: string): EntityFieldOption | undefined => {
  return entityFieldOptions.value.map(x => x.children).flat().find(x => x?.value == method);
}

const getFieldMethodTooltip = (method: string) => {
  return getFieldOptionByMethod(method)?.tooltip as string;
}

const onAppendField = () => {
  entityFields.value.push({ name: 'id', method: 'increment', type: 'number', params: '1,1', tooltip: '起始值, 步长' });
}

const onImport = () => {
  let table!: SqlParser.SqlTable;

  try {
    [table] = SqlParser.parse(createTableSql.value);
  } catch (error) {
    console.log(error);
    dialog.error({ title: '错误', content: '不是有效的建表语句', maskClosable: false, positiveText: '确定' });
    return;
  }

  entityName.value = table.name;
  entityFields.value.length = 0;

  let columns = table.columns.filter(x => x.type == 'column');
  columns.forEach(column => {
    let field = {
      name: column.name,
      method: '',
      params: '',
      tooltip: '',
      type: ''
    };
    if (column.auto_increment) {
      field.type = 'number';
      field.method = 'increment';
      field.params = '1,1';
    } else if (['sex', 'gender'].includes(column.name.toLowerCase()) && column.data_type.type.includes('CHAR')) {
      field.method = 'character';
      field.params = '"男女"';
    }
    let option = entityFieldOptions.value.map(x => x.children).flat().find(x => x?.value == field.method);
    if (option) field.tooltip = option.tooltip as string;
    entityFields.value.push(field);
  });

  isImportDialogVisible.value = false;
}

/**
 * 在字符串左右两边加上指定的符号
 * @param text 字符串
 * @param symbol 左右两边要加的符号
 */
const wrapText = (text: string, symbol: string) => {
  return `${symbol}${text}${symbol}`;
}

const onGenerate = () => {
  let names = entityFields.value.map(x => x.name);
  if (new Set(names).size != names.length) {
    dialog.error({ title: '错误', content: '列名不允许重复', maskClosable: false, positiveText: '确定' });
    return;
  }

  if (entityFields.value.some(x => !x.method)) {
    dialog.error({ title: '错误', content: '部分列未设置类型', maskClosable: false, positiveText: '确定' });
    return;
  }

  let generator: { [key: string]: any } = {};
  for (let field of entityFields.value) {
    if (field.method === 'increment') {
      generator[field.name] = eval(`Mock.Random.${field.method}(${field.params})`);
    } else {
      generator[field.name] = () => eval(`Mock.Random.${field.method}(${field.params})`);

    }
  }

  let data: any[] = [];
  for (let i = 0; i < entityCount.value; i++) {
    let item: any = {};
    for (let key of Object.keys(generator)) {
      item[key] = generator[key]();
    }
    data.push(item);
  }

  resultTableColumns.value.length = 0;
  resultTableColumns.value.push(...entityFields.value.map(x => ({ title: x.name, key: x.name, ellipsis: { tooltip: true } })));
  entities.value.length = 0;
  entities.value.push(...data);
  console.log(entities.value);

  jsonResult.value = JSON.stringify(data, null, 2);
  mysqlResult.value = entities.value.map(entity => {
    let keys = Object.keys(entity);
    let fields = keys.map(key => wrapText(key, "`")).join(', ');
    let values = keys.map(key => wrapText(entity[key], ["number"].includes(entityFields.value.find(x => x.name == key)?.type ?? '') ? "" : "'")).join(', ');
    return `insert into \`${entityName.value}\` (${fields}) values(${values});`;
  }).join('\n');

  activeTabName.value = "随机数据";
}


const onDebug = () => {
  console.log(entityFields.value);
  console.log(SqlParser.parse('SELECT * from aaa'));
}

onMounted(() => {
  entityFields.value.forEach(field => {
    let option = getFieldOptionByMethod(field.method);
    field.tooltip = option?.tooltip as string;
  })
});

onBeforeMount(() => {
  fetch('https://unpkg.com/mockjs/dist/mock-min.js').then(async response => {
    const code = await response.text();
    window.eval(code);
    window.eval(`
      Mock.Random.extend({
        increment: function (start, step) {
          var key = undefined;
          return function () {
            if (key === undefined) key = start - step;
            return key += step;
          }
        },
      })
    `);
  })
});


